


























import {Component, Prop, Vue} from "vue-property-decorator"
import { ITrainerProfile, ITrainingSession, TrainingSessionState } from "@chaosinsight/yogipeople-portalclient"
import Portal from "@/managers/portal"
import Zendesk from "@/managers/zendesk"
import i18n from "../i18n"
import { TranslateResult } from "vue-i18n"

@Component({ components: { } })
export default class FeedbackTrainer extends Vue {
	@Prop()
	public feedbackType!: string

	@Prop()
	public profile!: ITrainerProfile

	@Prop()
	public sessionData!: ITrainingSession

	public blockStudent: boolean = false
	public comment: string | null = null
	public cancelTime: string | null = null

	private storage = window.localStorage

	private cancelReasons: TranslateResult[] = [
		i18n.t("feedbackTrainer.reason1"),
		i18n.t("feedbackTrainer.reason2"),
		i18n.t("feedbackTrainer.reason3"),
		i18n.t("feedbackTrainer.reason4"),
		i18n.t("feedbackTrainer.reason5"),
	]

	private created(): void {
		this.cancelTime = this.feedbackType === "cancel" ? new Date().toLocaleString() : null

		// Zendesk ticket "session started": remove local storage item if exists
		if (this.storage.getItem("zendesk") !== null)
			this.storage.removeItem("zendesk")
	}

	/**
	 * Submit corresponding feedback
	 */

	private submitFeedback(cancelReason?: string) {
		if ([TrainingSessionState.Expired, TrainingSessionState.OtherUserCompleted].indexOf(this.sessionData.State) !== -1)
			this.completeOnLogin()
		else if (this.sessionData.State === TrainingSessionState.OtherUserCancelled)
			this.cancelOnLogin()
		this.postZendesk(cancelReason)
	}

	/**
	 * Complete/cancel session on login (if exists)
	 */

	private completeOnLogin(): void {
		Portal.client.call.trainingSession.trainerComplete(this.sessionData.Id)
			.response.then()
	}

	private cancelOnLogin(): void {
		Portal.client.call.trainingSession.cancel(this.sessionData.Id)
			.response.then()
	}

	/**
	 * Send Zendesk ticket
	 */

	private postZendesk(reason: string | null = null): void {
		Zendesk.createTicket(this.feedbackType, reason, this.profile, this.sessionData, this.cancelTime, null, this.blockStudent, this.comment)
		this.$emit("close")
	}
}
